import cx from "classnames";
import Description from "../Description";
import PageTitle from "../PageTitle";
import RoundButton from "../RoundButton";

const Hero = (props) => {
  const { connectWallet, burningStatus, endTime } = props;

  let days = 0;
  let hours = 0;
  let minutes = 0;
  const currentTime = new Date().getTime() / 1000;

  if (burningStatus === 1) {
    const timeToEnd = endTime - currentTime;
    days = Math.floor(timeToEnd / (60 * 60 * 24));
    hours = Math.floor((timeToEnd % (60 * 60 * 24)) / (60 * 60));
    minutes = Math.floor((timeToEnd % (60 * 60)) / 60);
  }

  return (
    <div className={cx("burning-hero")}>
      <div className={cx("page-intro")}>
        <PageTitle>
          loh <span>burn</span>
        </PageTitle>
        <Description classes="text-center">
          Ready to join the Humans ranks?
          <br />
          Burn your LOH NFTs and secure your spot
        </Description>
        {burningStatus === 1 ? (
          <>
            <Description classes="remaining-time text-center">
              {days}D {hours}H {minutes}M
              <br />
              <Description classes="remaining-time-sub">
                Left to the end of the burn
              </Description>
            </Description>
          </>
        ) : (
          <>
            <Description classes="remaining-time">
              CLOSED
              <br />
            </Description>
          </>
        )}

        <RoundButton
          variant="danger"
          text="connect wallet"
          onBtnClick={connectWallet}
        />
      </div>
    </div>
  );
};

export default Hero;
