import { useWeb3React } from "@web3-react/core";
import useSWR from "swr";
import { useState } from "react";
import Layout from "components/Layout";
import Hero from "components/Burn/Hero";
import LOHBurning from "components/Burn/LOHBurning";
import BurningContainer from "components/Burn/BurningContainer";
import { useMediaQuery } from "@uidotdev/usehooks";
import { getContract } from "config/contracts";
import { useChainId } from "lib/chains";
import { contractFetcher } from "lib/contracts";
import BurningLOH from "abis/BurningLOH.json";

import "./index.scss";

// ================================================

const Burn = (props) => {
  const { connectWallet, setPendingTxns } = props;

  const { active, library, account } = useWeb3React();
  const isMobile = useMediaQuery("only screen and (max-width: 640px)");

  const { chainId } = useChainId();
  const burningLOHAddress = getContract(chainId, "BurningLOH");

  const { data: startTimeData } = useSWR(
    true &&
      burningLOHAddress && [true, chainId, burningLOHAddress, "startTime"],
    { fetcher: contractFetcher(library, BurningLOH) }
  );

  const startTime = startTimeData ? parseInt(startTimeData.toString()) : 0;

  const { data: endTimeData } = useSWR(
    true &&
      burningLOHAddress && [true, chainId, burningLOHAddress, "getEndTime"],
    { fetcher: contractFetcher(library, BurningLOH) }
  );

  const endTime = endTimeData ? parseInt(endTimeData.toString()) : 0;
  const currentTime = new Date().getTime() / 1000;

  let burningStatus = 1;
  if (currentTime < startTime)
    // Not started
    burningStatus = 0;
  else if (currentTime >= startTime && currentTime <= endTime)
    // Burning
    burningStatus = 1;
  else if (currentTime > endTime)
    // Ended
    burningStatus = 2;

  return (
    <Layout>
      <BurningContainer isActive={active}>
        {!active || burningStatus !== 1 ? (
          <>
            <Hero
              connectWallet={connectWallet}
              burningStatus={burningStatus}
              endTime={endTime}
            />
          </>
        ) : (
          <LOHBurning isMobile={isMobile} setPendingTxns={setPendingTxns} />
        )}
      </BurningContainer>
    </Layout>
  );
};

export default Burn;
