import cx from "classnames";
import LOHModal from "../LOHModal";
import RoundButton from "../RoundButton";
import { LOH_RAFFLES } from "config/raffles"
import AvatarKYORotate from "assets/images/common/KYORotate.gif";
import DangerKeepOutImage from "assets/images/earn/DangerKeepOut.png";


// ================================================

const BurnConfirmModal = ({
  openModal,
  setOpenModal,
  onCloseModal,
  pointAmount,
  onConfirmBtn,
  isMobile,
}) => {
  return (
    <LOHModal
      open={openModal}
      setOpen={setOpenModal}
      onCloseModal={onCloseModal}
      classes="burn-confirm-modal"
    >
      <h4 className="title text-center uppercase mb-3">
        you will be receiving<br />
        {pointAmount}<br />
        LOH points
      </h4>
      <p className="desc text-center mb-5 uppercase italic">
        Press the below button to<br />
        initate the burn!
      </p>
      <div className="text-center mb-6">
        <RoundButton
          variant="danger"
          text="confirm"
          onBtnClick={onConfirmBtn}
        />
      </div>
    </LOHModal>
  );
};

export default BurnConfirmModal;
